@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
* {
}

body {
  background-color: #f1f1f1;
  margin: 0;
  font-family: "Open Sans", arial, sans-serif;
}

.gallery__viewport {
  max-width: 90vw;
  overflow-x: hidden;
  height: 15rem;
}

.gallery {
  display: flex;
}

.gallery__image {
  margin: 0 5px;
  max-width: auto;
  height: 14rem;
  opacity: 0.6;
}

.focused {
  opacity: 1;
  box-shadow: 2px 2px 10px #000;
}

.phone__header {
  background-color: white;
  width: 100%;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.phone__action {
  cursor: pointer;
}

.phone__options {
  flex-direction: column;
  margin-top: 8rem;
  justify-content: space-evenly;
  position: absolute;
  display: none;
  z-index: 1;
}

.phone__options a {
  background-color: white;
  color: black;
  text-align: center;
  text-decoration: none;
  margin: 0;
  padding: 0.6em 1.2rem;
  outline: 1px solid rgb(223, 219, 219);
}

.phone__options a:hover {
  cursor: pointer;
  background-color: rgb(243, 243, 243);
}

.copy-notification {
  transition: 0.5s ease-in-out;
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 1rem;
  right: 1rem;
  background-color: rgb(243, 243, 243);
  padding: 1rem;
  border-radius: 1rem;
  z-index: 1;
}

.copy-notification--visible {
  visibility: visible;
  opacity: 1;
}

.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 90vh;
}

.hero__title {
  font-family: "Playfair Display", Georgia, serif;
  font-weight: 400;
  font-size: 1.7rem;
}

.hero__subtitle {
  color: #646363;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.hero__image {
  width: 28rem;
  height: 25rem;
}

.hero__button {
  width: 28rem;
  margin: 1rem;
  padding: 1rem;
  text-transform: uppercase;
  border: none;
  opacity: 0.95;
  background-color: #161616;
  color: #dbdbdb;
}

.hero__button:hover {
  opacity: 1;
  cursor: pointer;
}

.section {
  min-height: 25rem;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Playfair Display", Georgia, serif;
}

.section__title {
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section__underline {
  margin-top: 2rem;
  margin-bottom: 1.5rem;
  background-color: #e2e2e2;
  width: 50px;
  height: 2px;
}

